
*{
  margin: 0;

}
body{
  background-color: white;
}
.game-name{
  display: block;
  color: black;
  max-width: 900px;
  background-color: white;
  padding: 1rem 0 ;
  margin: 2rem 1rem;
  border: none;
  border-radius: 0.5rem;
  text-align: center;
}
.title{
  font-size: clamp(1.4em, 2.5vw, 2.7rem);
}
.info{
  font-size: clamp(0.8em, 1.3vw, 1.1rem);
  width: 70%;
  margin: 0.5rem auto;
}
.main{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-template-rows: repeat(2, 100px);
  gap: 20px;
  background-color: #f1f1f1;
  max-width: 900px;
  border-radius: 0.5rem;
  margin: 3rem 1rem;
  justify-content: space-around;
  padding: 2rem;
  box-shadow: 0 6px 17px -5px #aaa;
}
@media (min-width:900px){
  .main{
    margin: 3rem auto;
  }
  .game-name{
    margin: 2rem auto;
    padding: 1rem 2rem;
  }
}
.die{
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  border-radius: 0.5rem;
  font-size: 2rem;
  text-align: center;
  align-items: center;
  margin: auto;
  cursor: pointer;
  box-shadow:  5px 5px 3px #5f5f5f,
             -4PX -4px 3px #ffffff;
  width: 90px;
  height: 80px;
 
}
.die-number{
  margin: 1.1rem auto;
  color: rgb(200, 6, 6);
}
.roll-btn{
    background-color:    #0051ff;
 padding: 0.5rem 0 ;
 border: none;
 border-radius: 0.3rem;
 color: white;
 font-size: clamp(1em, 2vw, 1.5rem);
 transition: all 100ms 10ms;
 cursor: pointer;
 letter-spacing: 0.1rem;
 width: 100%;
 font-weight: bold;
}
.roll-btn:hover{
  box-shadow: 0 0 12px -4px rgb(38, 247, 240);
  transform: scale(1.02, 1.02);
}
.button-container{
  max-width: 900px;
  margin: 1rem auto;
  display: grid;
  padding: 1rem ;
}
.win-container{
  width: 90%;
  margin:2rem auto;
  background-color: #0051ff;
  padding: 1rem;
  border-radius: 0.4rem;
}
.win-text{
 font-family: Arial, Helvetica, sans-serif;
 font-size: clamp(1.3em, 2.5vw, 4rem);
 color: white;
 text-align: center;
}